.carousel {
  display: flex;
  justify-content: center;
  overflow: hidden;
  position: relative;
  width: 1340px;
  height: 415px;
}

.carousel-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}

.carousel-counter-pictures {
  position: absolute;
  bottom: 10px;
  color: #FFFFFF;
  font-size: 18px;
  font-weight: 400;
}

.carousel-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  width: 96px;
  height: 112px;
  border: none;
  cursor: pointer;
  z-index: 1;
}

.prev-button {
  left: 0;
  background-image: url("../../assets/arrow-back.svg");
}

.next-button {
  right: 0;
  background-image: url("../../assets/arrow-next.svg");
}

.carousel-image {
  transition: opacity 0.5s ease-in-out;
}

@media screen and (max-width: 768px) {
  .carousel {
    width: 720px;
    height: 320px;
  }
  .carousel-image {
    border-radius: 15px;
  }
  .carousel-button {
    width: 50px;
    height: 50px;
    background-size: 90%;
  }
}


@media screen and (max-width: 431px) and (max-height: 933px) {
  .carousel {
    width: 355px;
    height: 255px;
  }
  .carousel-image {
    border-radius: 10px;
  }
  .carousel-counter-pictures {
    visibility: hidden;
  }
  .carousel-button {
    width: 30px;
    height: 30px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 80%;
  }
}

